.check-out-complete-popup {
  max-width: 600px; }
  .check-out-complete-popup .check-out-complete {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%; }
    .check-out-complete-popup .check-out-complete p {
      position: relative;
      z-index: 1;
      padding: 0 10px; }
    .check-out-complete-popup .check-out-complete img {
      max-width: 90%;
      opacity: 0; }
      @media screen and (max-width: 576px) {
        .check-out-complete-popup .check-out-complete img {
          max-width: 60%; } }
    .check-out-complete-popup .check-out-complete .bounce-in {
      animation: bounceIn 1s;
      animation-timing-function: linear;
      animation-fill-mode: forwards; }
    .check-out-complete-popup .check-out-complete .upper-container p,
    .check-out-complete-popup .check-out-complete .upper-container span {
      text-align: center;
      font-size: 24px;
      font-weight: 600;
      color: #354F53; }
      @media screen and (max-width: 576px) {
        .check-out-complete-popup .check-out-complete .upper-container p,
        .check-out-complete-popup .check-out-complete .upper-container span {
          font-size: 18px; } }
    .check-out-complete-popup .check-out-complete .upper-container .focus-text {
      color: #00D7AF; }
    .check-out-complete-popup .check-out-complete .upper-margin-up {
      margin-top: -50px; }
    .check-out-complete-popup .check-out-complete .upper-margin-down {
      margin-top: 30px; }
    .check-out-complete-popup .check-out-complete .extra-text p,
    .check-out-complete-popup .check-out-complete .extra-text span,
    .check-out-complete-popup .check-out-complete .mid-container p,
    .check-out-complete-popup .check-out-complete .mid-container span {
      text-align: center;
      font-size: 16px;
      font-weight: 200;
