      max-width: 350px; }
      @media screen and (max-width: 576px) {
        .check-out-complete-popup .check-out-complete .extra-text p,
        .check-out-complete-popup .check-out-complete .extra-text span,
        .check-out-complete-popup .check-out-complete .mid-container p,
        .check-out-complete-popup .check-out-complete .mid-container span {
          font-size: 14px; } }
    .check-out-complete-popup .check-out-complete .button-container {
      display: flex;
      margin-top: 27px; }
