.form-landing-splash-container {
  margin: 20% 0 5% 0;
  padding-right: 92px; }
  .form-landing-splash-container .mas-container {
    display: flex;
    align-items: center;
    margin: 50px 0 20px 0; }
    .form-landing-splash-container .mas-container .h7 {
      position: relative;
      z-index: 1;
      max-width: 150px;
      margin-left: 10px; }
      @media screen and (max-width: 1110px) {
        .form-landing-splash-container .mas-container .h7 {
          max-width: 120px; } }
    .form-landing-splash-container .mas-container img {
      width: 100px;
      height: auto;
      filter: saturate(0%); }
  .form-landing-splash-container h1 {
    position: relative;
    z-index: 1;
    max-width: 300px; }
  .form-landing-splash-container .splash-img {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 256px; }

.form-landing-insurer-carousel .insurer-logo-carousel {
  margin-top: 36px; }
  .form-landing-insurer-carousel .insurer-logo-carousel .carousel-container {
    width: 112%;
    transform: translateX(-6%); }
  .form-landing-insurer-carousel .insurer-logo-carousel .insurer-logo-sq {
    height: 60px; }
    .form-landing-insurer-carousel .insurer-logo-carousel .insurer-logo-sq img {
      object-fit: contain;
      width: 100px;
      height: 60px; }
